import React, { useState } from 'react';
import './Header.scss';

export default function Header() {
    const [showMenu, setShowMenu] = useState(false);

    function toggleNavigation() {
        setShowMenu(!showMenu);
    }

    return (
        <div className="container-fluid">
            <div className="row d-none d-xl-block s1">
                <nav className="container-fluid">
                    <div className="row first nav-padding">
                        <img src="https://www.letsbld.com/images/logos/logo-nzxt-black.png" alt="GRigs" />
                        <ul className="navbar-nav navbar-expand">
                            <li><a href="/what-is-bld">Sign In</a></li>
                            <li><a href="/starter-pc">Sign Up</a></li>
                        </ul>
                    </div>

                    <div className="row second nav-padding">
                        <img src="https://www.letsbld.com/images/logos/logo-bld.png" alt="GRigs" />

                        <ul className="navbar-nav navbar-expand">
                            <li><a href="/what-is-bld">What is BLD</a></li>
                            <li><a href="/starter-pc">Starter PC</a></li>
                            <li><a href="/streaming-pc">Streaming PC</a></li>
                            <li><a href="/h1-mini-pc">H1 MINI PC</a></li>
                            <li><a href="/creator-pc">Creator PC</a></li>
                            <li><a href="/reviews" className="">Customer Reviews</a></li>
                            <li><a target="_blank" href="#">Support</a></li>
                            <li className="started-button"><a href="#" className="btn">GET STARTED</a></li>
                        </ul>
                    </div>
                </nav>
            </div>

            <div className="row d-xl-none s2">
                <nav className="container-fluid">
                    <div className="row first nav-padding">
                        <img src="https://www.letsbld.com/images/logos/logo-nzxt-black.png" alt="GRigs" />
                        <ul className="navbar-nav navbar-expand">
                            <li><a href="/what-is-bld">Log In</a></li>
                            <li><a href="/starter-pc">Sign Up</a></li>
                        </ul>
                    </div>

                    <div className="row second nav-padding">
                        <img src="https://www.letsbld.com/images/logos/logo-bld.png" alt="GRigs" />

                        <i className="fa fa-bars hamburger-menu" onClick={toggleNavigation}></i>

                        <ul className={`navbar-nav w-100 ${!showMenu ? "hidden" : ""}`}>
                            <li><a href="/what-is-bld">What is BLD</a></li>
                            <li><a href="/starter-pc">Starter PC</a></li>
                            <li><a href="/streaming-pc">Streaming PC</a></li>
                            <li><a href="/h1-mini-pc">H1 MINI PC</a></li>
                            <li><a href="/creator-pc">Creator PC</a></li>
                            <li><a href="/reviews" className="">Customer Reviews</a></li>
                            <li><a target="_blank" href="#">Support</a></li>
                            <li className="started-button"><a href="#" className="btn">GET STARTED</a></li>
                        </ul>
                    </div>
                </nav>
            </div>
        </div>
    );
}